body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.flex-row{
  display: flex;
}

.flex-between{
  display: flex;
  justify-content: space-between;
}

.flex-around{
  display: flex;
  justify-content: space-around;
}

.flex-column{
  display: flex;
  flex-direction: column;
}

.items-center{
  align-items: center;
}

.gap-05{
  gap: 0.5rem;
}

.gap-1{
  gap: 1rem;
}

.gap-3{
  gap: 3rem;
}

.mt-05{
  margin-top: 0.5rem;
}

.mt-1{
  margin-top: 1rem;
}

.mt-3{
  margin-top: 3rem;
}

.mb-05{
  margin-bottom: 0.5rem;
}

.mb-1{
  margin-bottom: 1rem;
}

.mb-3{
  margin-bottom: 3rem;
}

